___CSS_LOADER_EXPORT___.push([module.id, `.header {
  padding: 10px 24px;
  display: flex;
  justify-content: space-between;
  max-width: 1440px;
  margin: 0 auto;
}
.header__left {
  padding-top: 8px;
}
.header__left .burger {
  display: none;
}
.header__right {
  display: flex;
  align-items: center;
  flex-basis: auto;
}
.header__menu {
  display: none;
}
.header .search {
  margin-right: 38px;
}
.header .ring {
  margin-right: 14px;
}
.header__button {
  padding: 10.5px 35px;
  margin-left: 15px;
}
.header__profile {
  position: relative;
  margin-left: 24px;
  display: flex;
  align-items: center;
  cursor: pointer;
  z-index: 1;
  color: #bbb;
}
.header__profile_img {
  margin-right: 10px;
  width: 24px;
  height: 24px;
  border-radius: 24px;
}
.header__video {
  transition: 0.3s;
  position: relative;
  color: #bbb;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
  margin-left: 24px;
  z-index: 1;
}
.header__video:active {
  color: #fff;
}
.header__video:active:after {
  border-radius: 8px;
  z-index: -1;
  background: #222;
  position: absolute;
  padding: 15px;
  width: 120%;
  left: -10%;
  content: "";
}
.header__store {
  transition: 0.3s;
  color: #bbb;
  position: relative;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
  margin-left: 24px;
}
.header__store:active {
  color: #fff;
}
.header__store:active:after {
  border-radius: 8px;
  z-index: -1;
  background: #222;
  position: absolute;
  padding: 15px;
  width: 120%;
  left: -10%;
  content: "";
}
.header__authed {
  flex-basis: auto;
}
.header__authed .search {
  margin-left: 0px;
  margin-right: 47px;
}
.header__authed .ring {
  margin-right: 37px;
}
.header .header__authed-block {
  display: flex;
  flex-basis: auto;
}
.header .header__video {
  margin: 0 9px;
  align-items: center;
  display: flex;
}
.header .header__store {
  align-items: center;
  display: flex;
  margin: 0 34px;
}
.header .header__profile {
  margin-right: 11px;
  margin-left: 11px;
}
.header .profile__text {
  color: #bbb;
  font-size: 16px;
  font-weight: 400;
}
.header .profile__popup {
  border-radius: 8px;
  background: #222;
  position: absolute;
  padding: 16px;
  gap: 10px;
  display: flex;
  flex-direction: column;
  bottom: -90px;
  width: 150px;
  left: -60px;
}
.header .profile__popup svg {
  margin-right: 8px;
}
.header .profile__edit {
  display: flex;
  align-items: center;
}
.header .profile__edit_text {
  color: #bbb;
  font-size: 16px;
  font-weight: 400;
}

@media screen and (max-width: 575px) {
  .header {
    padding: 15px 23px;
    position: relative;
  }
  .header .header__video, .header .header__store {
    display: none;
  }
  .header__menu {
    z-index: 1;
    display: none;
    position: absolute;
    left: 0;
    padding-left: 16px;
    top: 50px;
    background: var(--9, #222);
    height: 100vh;
    width: 294px;
  }
  .header__menu_active {
    display: block;
  }
  .header__menu_ul {
    margin-top: 24px;
  }
  .header__menu_li {
    padding: 10px 14px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 400;
  }
  .header__left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .header__left .burger {
    display: block;
    margin-right: 21px;
  }
  .header__left .cross {
    display: block;
    margin-right: 21px;
  }
  .header__right {
    display: flex;
    flex-basis: auto;
    justify-content: flex-end;
    justify-content: space-between;
    align-items: center;
  }
  .header__right .search {
    margin: 0;
  }
  .header .ring {
    display: none;
  }
  .header__button {
    display: none;
  }
